import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import { ConfigProvider, Epic, Tabbar, TabbarItem, View } from '@vkontakte/vkui';
import Icon28NewsfeedOutline from '@vkontakte/icons/dist/28/newsfeed_outline';
import Icon28StudOutline from '@vkontakte/icons/dist/28/stud_outline';
import Icon28More from '@vkontakte/icons/dist/28/more';
import Home from './panels/Home';
import Home1 from './panels/Home1';
import Home2 from './panels/Home2';
import Home3 from './panels/Home3';
import Persik from './panels/Persik';
import Icon28BillheadOutline from '@vkontakte/icons/dist/28/billhead_outline';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
   

const App = () => {
  const [activePanel, setActivePanel] = useState("home");
  const [fetchedUser, setUser] = useState(null);
  const [loading, setLoading] = useState(<ScreenSpinner size="large" />);
  const [schema]=React.useState('bright_light')
  const [activeStory,changeActiveStory]=React.useState('feed')
  const [tabbar]=React.useState(true)
  const [user]=React.useState(null)
  const [achivments, changeAchivments] = React.useState([])
  const onStoryChange = (e) => {
    const a = e.currentTarget.dataset.story;
    changeActiveStory(a?a:'feed')
  }
  const walk = () => {
    changeActiveStory('instr')
    }

 
  const go = (e) => {
    setActivePanel(e.currentTarget.dataset.to);
  };

const pressed = (maskName) => {
fetch('https://mighty-temple-55739.herokuapp.com/published'+window.location.search+'&mask_id='+maskName)
}


React.useEffect(()=>{
bridge.subscribe((e) => {
const { type, data } = e.detail;})
fetch('https:\/\/mighty-temple-55739.herokuapp.com/user'+window.location.search) 
.then(e=>e.text()) 
.then(e=>changeAchivments(JSON.parse(e)))
},[])

  /* TODO */
  const [todos, setTodos] = useState([]);

  useEffect(() => {
    fetch("https://jsonplaceholder.typicode.com/todos?_limit=10")
      .then((response) => response.json())
      .then((todos) => {
        setTodos(todos);
        setLoading(null);
      });
  }, []);

  function toggleTodo(id) {
    setTodos(
      todos.map((todo) => {
        if (todo.id === id) {
          todo.completed = !todo.completed;
        }
        return todo;
      })
    );
  }

  function removeTodo(id) {
    setTodos(todos.filter((todo) => todo.id !== id));
  }

  function addTodo(title) {
    setTodos(
      todos.concat([
        {
          title,
          id: Date.now,
          completed: false,
        },
      ])
    );
  }
  return (
    <ConfigProvider isWebView={true} scheme={schema}>
      <Epic activeStory={activeStory} tabbar={tabbar&&
        <Tabbar style={{visibility: 'hidden'}}>
          <TabbarItem
            onClick={onStoryChange}
            selected={activeStory === 'feed'}
            data-story="feed"
            text="Маски"
          ><Icon28NewsfeedOutline /></TabbarItem>
          <TabbarItem
            onClick={onStoryChange}
            selected={activeStory === 'notifications'}
            data-story="notifications"
            text="Достижения"
          ><Icon28StudOutline /></TabbarItem>
          <TabbarItem
            onClick={onStoryChange}
            selected={activeStory === 'profil'}
            data-story="profil"
            text="Новости"
          ><Icon28BillheadOutline /></TabbarItem>
          <TabbarItem
            onClick={onStoryChange}
            selected={activeStory === 'more'}
            data-story="more"
            text="Ещё"
          ><Icon28More /></TabbarItem>          
        </Tabbar>
      }>
        <View id="feed" activePanel="home">
        <Home pressed={pressed} id="home" fetchedUser={fetchedUser} go={go} />
        </View>

        <View id="notifications" activePanel="home">
        <Home1 id="home" fetchedUser={user} go={go} achivments={achivments} />
        </View>

        <View id="more" activePanel="home">
        <Home2 id="home" walk={walk}/>
        </View>

        <View id="profil" activePanel="home">
        <Home3 id="home" fetchedUser={user} go={go} />
        </View>        

        <View id="instr" activePanel="home">
        <Persik id="home" fetchedUser={user} walk={walk}  />
        </View>   

      </Epic>
    </ConfigProvider>
  );
}
export default App;
 



